var Sizzle = require('sizzlejs');
var $ = Sizzle;

var loadedImages = {};

export const HTML_SPACE = '&nbsp;';
export const HREF_VOID = 'javascript: void(0);';

export type WebpCallbackType = (supported: boolean) => void;

export type AppHooksType = {
    onwebp: WebpCallbackType;
};

export enum ApplicationType {
    index,
    features
}

let appType: ApplicationType = null;

export function getAppType(): ApplicationType {
    if (appType === null) {
        appType = ApplicationType.index;
        const href = location.href.toLowerCase();
        if (href.indexOf('features.html') > -1) {
            appType = ApplicationType.features;
        }
    }
    return appType;
}

export function preloadImages(webpSupported: boolean, imagesToPreload: { [elementId: string]: string }) {
    for (var imgId in imagesToPreload) {
        if (imagesToPreload.hasOwnProperty(imgId)) {
            var imageFile = imagesToPreload[imgId];
            preloadImg(imageFile, webpSupported, imgId);
        }
    }
}

